export default {
  coaches(state) {
    return state.coaches;
  },
  hasCoaches(state) {
    return state.coaches && state.coaches.length > 0;
  },
  selectedCoaches(state) {
    const selectedCoaches = state.coaches.filter((coach) => {
      return coach.areas.some((area) => state.filter.includes(area));
    });
    return selectedCoaches;
  },

  isCoach(_, getters) {
    const coaches = getters.coaches;
    const userId = getters.userId;
    return coaches.some((coach) => coach.id === userId);
  },
  hasRequests(_, getters) {
    return getters.requests && getters.requests.length > 0;
  },
  requests(state, getters) {
    const coachId = getters.userId;
    return state.requests.filter((request) => request.coachId === coachId);
  },
  shouldUpdate(state) {
    const lastFetch = state.lastFetch;
    if (!lastFetch) {
      return true;
    } else {
      const currentTimeStamp = new Date().getTime();
      return (currentTimeStamp - lastFetch) / 1000 > 60; //true if it was more than a minute ago
    }
  },
};
