<template>
    <div>
        <base-dialog :show="!!error" title="Something went wrong" @close="handleError">
            <p>{{ error }}</p>
        </base-dialog>
        <section>
            <coach-filter></coach-filter>
        </section>
        <section>
            <base-card>
                <div class="controls">
                    <base-button link mode="outline" @click="loadCoaches(true)">Refresh</base-button>

                    <base-button link to="/auth" v-if="!isLoggedIn">Login to Register as Coach</base-button>
                    <base-button v-if="isLoggedIn && !isCoach && !isLoading" link to="/register">Register as
                        Coach</base-button>
                </div>
                <div v-if="isLoading">
                    <base-spinner></base-spinner>
                </div>
                <ul v-else-if="hasCoaches">
                    <coach-item v-for="coach in selectedCoaches" :key="coach.id" :id="coach.id"
                        :first-name="coach.firstName" :last-name="coach.lastName" :rate="coach.hourlyRate"
                        :areas="coach.areas">{{ coach.firstName }} {{
                            coach.lastName }}
                    </coach-item>
                </ul>
                <h3 v-else>No coaches found.</h3>
            </base-card>
        </section>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
import CoachItem from '../../components/coaches/CoachItem.vue';
import CoachFilter from '../../components/coaches/CoachFilter.vue';

export default {
    components: {
        CoachItem, CoachFilter
    },
    data()
    {
        return {
            isLoading: false,
            error: null,
        };
    },
    created()
    {
        this.loadCoaches();
    },
    methods:
    {
        async loadCoaches(refresh = false)
        {
            this.isLoading = true;
            try {
                await this.$store.dispatch('loadCoaches', { forceRefresh: refresh });
            } catch (error) {
                this.error = error.message || "Something went wrong!";
            }
            this.isLoading = false;
        },
        handleError()
        {
            this.error = null;
        },
    },
    computed: {
        isLoggedIn()
        {
            return this.$store.getters.isAuthenticated;
        },
        isCoach()
        {
            return this.$store.getters.isCoach;
        },
        hasCoaches()
        {
            return !this.isLoading && this.$store.getters['hasCoaches'];
        },
        ...mapGetters(['coaches', 'selectedCoaches']),
    },
}
</script> 
<style scoped>
ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.controls {
    display: flex;
    justify-content: space-between;
}
</style>