<template>
    <section>
        <base-card>
            <div class="form-control">
                <h2>Select by area</h2>
                <div>
                    <input id="frontend" name="area" type="checkbox" value="frontend" class="filter-option" v-model="area"
                        @change="setFilter" />
                    <label for="frontend">FrontEnd</label>
                    <input id="backend" name="area" type="checkbox" value="backend" class="filter-option" v-model="area"
                        @change="setFilter" />
                    <label for="backend">BackEnd</label>
                    <input id="career" name="area" type="checkbox" value="career" class="filter-option" v-model="area"
                        @change="setFilter" />
                    <label for="career">Career</label>
                </div>
            </div>
        </base-card>
    </section>
</template>
<script>

export default {
    data()
    {
        return {
            area: ['frontend', 'backend', 'career'],
        };
    },
    methods: {
        setFilter()
        {
            this.$store.dispatch('filterCoaches', this.area);
        },
    },
    mounted()
    {
        this.setFilter()
    }
}
</script>
<style>
h2 {
    margin: 0.5rem 0;
}

.filter-option {
    margin-left: 1rem;
}

.filter-option label,
.filter-option input {
    vertical-align: middle;
}

.filter-option label {
    margin-left: 0.25rem;
}

.filter-option.active label {
    font-weight: bold;
}
</style>