export default {
  filterCoaches(context, payload) {
    context.commit('filterCoaches', payload);
  },
  //Register coach
  async registerCoach(context, payload) {
    const userId = context.getters.userId;
    const coachData = {
      firstName: payload.firstName,
      lastName: payload.lastName,
      areas: payload.areas,
      description: payload.description,
      hourlyRate: payload.hourlyRate,
    };
    const token = context.getters.token;
    const response = await fetch(
      `https://vue-http-demo-55f3c-default-rtdb.firebaseio.com/coaches/${userId}.json?auth=` +
        token,
      {
        method: 'PUT',
        body: JSON.stringify(coachData),
      }
    );

    // const responseData = await response.json();

    if (!response.ok) {
      // error...
    }

    context.commit('registerCoach', {
      ...coachData,
      id: userId,
    });
    context.commit('setCoaches');
  },

  //Load coaches
  async loadCoaches(context, payload) {
    if (!payload.forceRefresh === true && !context.getters.shouldUpdate) {
      return;
    }
    const response = await fetch(
      `https://vue-http-demo-55f3c-default-rtdb.firebaseio.com/coaches.json`
    );
    const responseData = await response.json();

    if (!response.ok) {
      const error = new Error(responseData.message || 'Failed to fetch!');
      throw error;
    }
    const coaches = [];
    for (const key in responseData) {
      const coach = {
        id: key,
        firstName: responseData[key].firstName,
        lastName: responseData[key].lastName,
        areas: responseData[key].areas,
        description: responseData[key].description,
        hourlyRate: responseData[key].hourlyRate,
      };
      coaches.push(coach);
    }
    context.commit('setCoaches', coaches);
    context.commit('setFetchTimestamp');
  },
  //Contact coach
  async contactCoach(context, payload) {
    const requestData = {
      email: payload.email,
      message: payload.message,
      to: payload.to,
    };
    const response = await fetch(
      `https://vue-http-demo-55f3c-default-rtdb.firebaseio.com/requests/${payload.coachId}.json`,
      {
        method: 'POST',
        body: JSON.stringify(requestData),
      }
    );
    const responseData = await response.json();
    if (!response.ok) {
      const error = new Error(
        responseData.message || 'Failed to send request.'
      );
      throw error;
    }

    requestData.id = responseData.name; //unique id from firebase
    requestData.coachId = responseData.id;
    context.commit('saveRequest', requestData);
  },

  //Fetch requests
  async fetchRequests(context) {
    const coachId = context.getters.userId;
    const token = context.getters.token;
    const response = await fetch(
      `https://vue-http-demo-55f3c-default-rtdb.firebaseio.com/requests/${coachId}.json?auth=` +
        token
    );
    const responseData = await response.json();

    if (!response.ok) {
      const error = new Error(
        responseData.message || 'Failed to fetch requests.'
      );
      throw error;
    }
    const requests = [];

    for (const key in responseData) {
      const request = {
        id: key,
        coachId: coachId,
        email: responseData[key].email,
        message: responseData[key].message,
        to: responseData[key].to,
      };
      requests.push(request);
    }
    context.commit('setRequests', requests);
  },
};
