import { createRouter, createWebHistory } from 'vue-router';
import { store } from 'vuex';
//Components
// import CoachDetails from './pages/coaches/CoachDetails.vue';
import CoachesList from './pages/coaches/CoachesList.vue';
// import CoachRegistration from './pages/coaches/CoachRegistration.vue';
// import CoachContact from './pages/requests/CoachContact.vue';
// import Requests from './pages/requests/Requests.vue';
// import UserAuth from './pages/auth/UserAuth.vue';
import NotFound from './pages/NotFound.vue';

const CoachDetails = () => import('./pages/coaches/CoachDetails.vue');
const CoachRegistration = () => import('./pages/coaches/CoachRegistration.vue');
const CoachContact = () => import('./pages/requests/CoachContact.vue');
const Requests = () => import('./pages/requests/Requests.vue');
const UserAuth = () => import('./pages/auth/UserAuth.vue');
const site = '/vuejs-app';
const router = createRouter({
  history: createWebHistory(),
  routes: [
    { path: '/', redirect: site + '/coaches' },
    { path: site + '/coaches', component: CoachesList },
    {
      path: site + '/coaches/:id',
      component: CoachDetails,
      props: true,
      children: [
        { path: 'contact', component: CoachContact }, // /coaches/c1/contact
      ],
    },
    { path: site + '/register', component: CoachRegistration },
    { path: site + '/requests', component: Requests },
    { path: site + '/auth', component: UserAuth },
    { path: site + '/404', component: NotFound },
    { path: '/:notFound(.*)', redirect: '/404' },
  ],
  linkActiveClass: 'active',
});

router.beforeEach(function (to, _, next) {
  if (to.meta.requiresAuth && !store.getters.isAuthenticated) {
    next('/auth');
  } else if (to.meta.requiresUnauth && store.getters.isAuthenticated) {
    next('/coaches');
  } else {
    next();
  }
});

export default router;
